import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'index',
    component: () =>
      import('@/views/layout/main.vue'),
    children: [{
      path: '/home',
      name: 'home',
      component: () =>
        import('@/views/home/index.vue')
    }, {
      path: '/demo',
      name: 'demo',
      component: () =>
        import('@/views/demo/index.vue')
    }]
  }]
})



